
html {
  font-family: $default-font;
}
// title text
.tit-h1 {
  font-size: $font-54r;
  font-weight: $b;
  line-height: $ln-h-w;
}
.tit-h2 {
  font-size: $font-48r;
  font-weight: $b;
  line-height: $ln-h-w;
}
.tit-h3 {
  font-size: $font-38r;
  font-weight: $b;
  line-height: $ln-h-w;
}
.tit-h4 {
  font-size: $font-36r;
  font-weight: $md;
  line-height: $ln-h-w;
}
.tit-h5 {
  font-size: $font-32r;
  font-weight: $md;
  line-height: $ln-h-w;
}
.tit-h6 {
  font-size: $font-24r;
  font-weight: $md;
  line-height: $ln-h-w;
}

// text
.txt-1 {
  font-size: $font-16r;
  font-weight: $r;
  line-height: $ln-h-r;
}
.txt-2 {
  font-size: $font-16r;
  font-weight: $md;
  line-height: $ln-h-r;
}

// label text
.lb-txt-1 {
  font-size: $font-18r;
  font-weight: $md;
  line-height: $ln-h-w;
}
.lb-txt-2 {
  font-size: $font-16r;
  font-weight: $md;
  line-height: $ln-h-w;
}

// color
.royal-blue {
  background-color: $royal-blue;
}
.dark-blue {
  background-color: $dark-blue;
}
.grey {
  background-color: $grey;
}
.yellow {
  background-color: $yellow;
}
.tint-blue {
  background-color: $tint-blue;
}
.accent {
  background-color: $accent;
}
.light-yellow {
  background-color: $light-yellow;
}

// button
button{
  cursor: pointer;
}
.btn-ty01{
  background-color: $yellow;
  transition: background-color 0.3s;
  color: #1b1c2b;
  border-radius: 40px;
}
.btn-ty01:hover{
  background-color: $yellow-hover;
}
.btn-ty02{
  background-color: $dark-blue;
  transition: background-color 0.3s;
  color: $white;
  border-radius: 40px;
}
.btn-ty02:hover{
  background-color: $dark-blue-hover;
}