.style-guide .header-wrap { border-bottom: 1px solid rgba(35, 37, 54, 0.12); padding: 40px 0; }

.style-guide .header-wrap header { max-width: 1280px; width: 100%; margin: 0 auto; }

.style-guide .header-wrap header .tit-h4 { line-height: 44px; font-weight: 400; }

.style-guide .style-container { max-width: 1280px; width: 100%; margin: 0 auto; padding: 90px 0; }

.style-guide .style-container .style-content { margin-top: 90px; }

.style-guide .style-container .style-content:first-child { margin-top: 0; }

.style-guide .style-container .style-content .style-title .tit-h5 { font-weight: 400; }

.style-guide .style-container .style-content ul { margin-top: 30px; }

.style-guide .style-container .style-content ul li button { width: 222px; height: 64px; }

.font-area { display: flex; gap: 90px; }

.font-area .text-list { width: 50%; }

.font-area .text-list .text-item { margin-top: 32px; }

.font-area .text-list .text-item:first-child { margin-top: 0; }

.font-area .text-list .text-item:last-child { display: flex; gap: 38px; }

.font-area .text-list .text-item .tit-h6 { font-weight: 600; }

.font-area .text-list .text-item p { margin-top: 8px; }

.color-list { display: flex; flex-grow: 1; }

.color-list .color-item { width: calc(100% / 7); height: 100px; }

.color-list .color-item p { text-align: center; line-height: 100px; }

.button-list { display: flex; gap: 50px; }
